body {
  height: 100vh;
  margin: 0;
}

.block {
  display: grid;
  grid-template-columns: calc(25vh + 220px) auto;
}

/* Menu style */

.circle {
  border-radius: 50%;
  position: fixed;
  border: solid 30px #d31328;
  top: -15px;
  left: -80vh;
  width: calc(100vh - 30px);
  height: calc(100vh - 30px);
}

.nav-item {
  display: none;
}

 .active {
  color: #000000;
  display: inline;
}
